.banner-img{
    width:100%;
    height:400px
}
.title-desc{
    text-align: center;
    font-size: 26px;
    font-weight: bold;
    color:tomato;
}
.subtitle-desc{
    color: white;
    font-size: 18px;
    font-weight: bold;
    text-align: center;
}
.images{
    width: 60%;
    height: 60%;
    border-radius: 50%;
    border: 3px solid tomato;
}
.img-col{
    text-align: center;
}
.img-cols{
    text-align: center;
    display: inline-table;
}
.img-descr{
    font-size: 12px;
    font-weight: bold;
    color: white;
    margin-bottom: 0;
}
.glass-image{
    width: 50%;
    border-radius: 50%;
    height:75%;
}
.buynow-btn{
    background-color: white;
    border: none;
    font-size: 12px;
    font-weight: bold;
    border-radius: 10px;
    margin-top: 15px;
    width:85px;
    padding: 5px;
}
.buynow-btn a{
    color: black !important;
    text-decoration: none !important;
}
.line{
    height: 2px;
    width: 5%;
    background-color: white;
    margin-left: 47%;
    margin-top: 2em;
}
.terms-block{
    padding: 0px 45px 45px 45px;
    color: white;
}
.support-block{
    font-size:14px;
}
.email-dial{
    border: 1px solid white;
    margin-right:5px;
}
li{
    font-size: 13px;
    line-height: 1.3rem;
}
hr{
    width: 5%;
    height: 2px !important;
    background-color: white !important;
}
.wraptext{
    font-weight: bold;
    position: absolute;
    top: 100px;
    padding: 100px;
    color: white;
    font-size: 16px;
    text-align: center;
}
.collectionbtn{
    background-color: tomato;
    border-radius: 10px;
    border: none;
    width:150px;
    margin-top:10px;
}
.collectionbtn a{
    color: white;
    text-decoration: none;
    font-size: 12px;
    font-weight: bold;
}
.email-col{
    margin-right: 1%
}
.collectionimg{
    width: 100%;
    height:200px;
}
.wrapcollection{
    font-weight: 800;
    position: absolute;
    margin-top:75px !important;
    color: black;
    font-size: 80%;
    text-align: center;
    width: 100%;
    word-wrap: break-word;
}
.normalfont{
    font-size:14px;
}